<template>
    <v-container>
        <v-dialog v-model="activeEditDialog" persistent max-width="800px">
            <v-form ref="submitForm">
                <v-card>
                    <v-app-bar>
                        <v-btn
                                icon
                                color="indigo"
                                @click="changeDialogStatus"
                        >
                            <v-icon>mdi-close</v-icon>
                        </v-btn>

                        <v-toolbar-title></v-toolbar-title>
                        <v-spacer></v-spacer>
                        <v-btn
                                icon
                                color="indigo"
                                @click="commitRegister"
                        >
                            <v-icon>mdi-content-save</v-icon>
                        </v-btn>
                    </v-app-bar>
                    <v-list subheader>
                        <v-list-item>
                            <v-text-field
                                    label=""
                                    v-model="register.shopname"
                                    :counter="15"
                                    :rules="[v => !!v || '']"
                            >
                            </v-text-field>
                        </v-list-item>
                        <v-list-item>
                            <v-text-field
                                    label=""
                                    v-model="register.adress"
                                    :counter="25"
                                    :rules="[v => !!v || '']"
                            >
                            </v-text-field>
                        </v-list-item>
                        <v-list-item>
                            <v-radio-group row label="" v-model="register.cateid">
                                <v-radio label="" :value="1"></v-radio>
                                <v-radio label="" :value="2"></v-radio>
                                <v-radio label="" :value="3"></v-radio>
                                <v-radio label="" :value="4"></v-radio>
                            </v-radio-group>
                        </v-list-item>
<!--                        <v-list-item>-->
<!--                            <v-text-field-->
<!--                                    label="logoURL"-->
<!--                                    v-model="register.images"-->
<!--                                    clearable-->
<!--                            >-->
<!--                            </v-text-field>-->
<!--                        </v-list-item>-->
                        <v-subheader></v-subheader>
                        <v-list-item>
                            <v-text-field
                                    label="URL"
                                    v-model="register.permitImg"
                            >
                            </v-text-field>
                        </v-list-item>
                    </v-list>
                </v-card>
            </v-form>
        </v-dialog>
        <v-snackbar top v-model="successSaved" color="success">
            <v-alert type="success">
                
            </v-alert>
        </v-snackbar>
        <v-snackbar top v-model="failedSaved" color="info">
            <v-alert type="info">
                
            </v-alert>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapGetters, mapMutations} from "vuex";

    export default {
        name: "RegisterShopDialog",
        props: ['activeEditDialog', 'changeDialogStatus'],
        data() {
            return {
                register: {
                    "shopownerId": null,
                    "cateid": 2,
                    "shopname": "",
                    "adress": "",
                    "permitImg":null,
                    "images":null
                },
                activeEditDialogTest: true,
                timePickerStart: false,
                timePickerEnd: false,
                successSaved: false,
                failedSaved: false,
                "exampleShopregister": {
                    "shopownerId": 2,
                    "cateid": 1,
                    "shopname": "",
                    "adress": "225",

                },
            }
        },
        methods: {
            commitRegister() {
                if(!this.$refs.submitForm.validate()){
                    return
                }
                this.register.shopownerId=this.userInfo.id
                if(this.register.images===""){
                    this.register.images=null
                }
                if(this.register.permitImg===""){
                    this.register.permitImg=null
                }
                console.log(this.register)
                this.axios({
                    method: 'post',
                    url: '/shopregister/save',
                    data: this.register,
                    headers: {'content-type': 'application/json'}
                }).then(
                    res => {
                        if (res.data.code === 0) {
                            this.successSaved = true
                            this.changeDialogStatus()
                        }
                    }
                ).catch(
                    () => {
                        this.failedSaved = true
                    }
                )
            },
            ...mapMutations('validate', {
                setShopByShopId: 'SET_SHOP_BY_SHOP_ID'
            }),
        },
        computed: {
            ...mapGetters('validate', {
                userInfo: 'userInfo'
            })
        },
        watch: {},
        mounted() {
        }
    }
</script>

