<template>
    <v-card>
        <v-card-title></v-card-title>
        <v-card-text>
            <template>
                <v-form @submit="loginAndValidate" ref="form">
                    <v-container>
                        <v-row>
                            <v-col :offset-sm="3" :offset-md="3" :sm="6" :md="6">
                                <v-text-field
                                        v-model="username"
                                        :rules="notEmptyUserName"
                                        label=""
                                        required
                                        clearable
                                ></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col :offset-sm="3" :offset-md="3" :sm="6" :md="6">
                                <v-text-field
                                        v-model="password"
                                        :rules="notEmptyPassword"
                                        type="password"
                                        label=""
                                        required
                                        clearable
                                ></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col :offset-sm="3" :offset-md="3" :sm="6" :md="6">
                                <v-checkbox v-model="rememberMe" label=""/>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col :offset-sm="3" :offset-md="3" :sm="3" :md="3">
                                <v-btn color="success" type="submit"></v-btn>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-form>
            </template>
        </v-card-text>
    </v-card>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: "Login",
        data: () => ({
            valid: false,
            username: '',
            password: '',
            rememberMe:true,
            notEmptyUserName: [
                v => !!v || ''
            ],
            notEmptyPassword: [
                v => !!v || ''
            ]
        }),
        methods: {
            loginAndValidate() {
                if(this.$refs.form.validate()) {
                    this.userLogin(
                        {
                            username: this.username,
                            password: this.password,
                            'remember-me': this.rememberMe
                        }
                    )
                    this.$router.push('/home')
                    setTimeout(this.successAndReloadApp,2000)
                    // this.successAndReloadApp()
                }
            },
            successAndReloadApp(){
                location.reload()
            },
            ...mapActions('validate', {
                userLogin: 'userLogin'
            }),
        }
    }
</script>

