<template>
    <v-col :md="4">
        <v-card>
            <v-card-title>{{shopInfo.shopname}}</v-card-title>
            <v-card-text>{{promote.applayTime}}</v-card-text>
            <v-card-text>{{promote.promoteStatus}}</v-card-text>
            <v-card-text>{{getDate(new Date(promote.gmtCreate))}}</v-card-text>
        </v-card>
    </v-col>
</template>
<script>
    import {dateFormat} from "../../util";

    export default {
        name: 'PromoteItem',
        props: ['promote'],
        data() {
            return {
                shopInfo: {}
            }
        },
        created() {
            this.apiRequestShopId()
        },
        methods: {
            modifiedTime(date) {
                // date.setHours(date.getHours()+8)
                return dateFormat('HH:MM', date)
            },
            getDate(date) {
                // date.setHours(date.getHours()+8)
                return dateFormat('mmdd', date)
            },
            apiRequestShopId() {
                this.axios.get('/shopinfo/info/' + this.promote.shopId)
                    .then(
                        res => {
                            this.shopInfo =res.data.shopInfo
                        }
                    )
            }
        }
    }
</script>
